import React, {useEffect, useState, useRef } from "react";
import Navigation from '../Navigation/Navigation';
import "./itermap.scss"
const { kakao } = window;


const Itermap = () => {
    let temp;
    const [lat, setLat] = useState(35.1782720807211);
    const [lon, setLon] = useState(126.90176721598525);
    const [searchValue, setsearchValue] = useState("");
    let latLon = [lat, lon];
    let input = document.getElementById("input");
    
    //    (   )
    function currentLocation() {
        if(navigator.geolocation){
            navigator.geolocation.getCurrentPosition(function(position){
                setLat(position.coords.latitude);
                setLon(position.coords.longitude);
            });
        } else{
            console.log("       .");
            setLat(33.462182094543486);
            setLon(126.32865029284024);
        }
    };

    //     
    function changeLocation(e){
        temp = e.target.value;
    }

    //        
    function callUse(){
        setsearchValue(temp);
    }
    
    //  
    function resetMap(){
        input.value = null;
        setsearchValue("");
    }

    //   ( )
    useEffect( () => {
        currentLocation();
        
        let container = document.getElementById("map");
        let options = {
            center: new kakao.maps.LatLng(lat, lon),
            level: 3
        };
        let map = new kakao.maps.Map(container, options); //    

    }, [latLon]);

    //      
    useEffect( () => {
        let infowindow = new kakao.maps.InfoWindow({zIndex:1});

        let container = document.getElementById("map");
        let options = {
            center: new kakao.maps.LatLng(lat, lon),
            level: 1
        };
        let map = new kakao.maps.Map(container, options); //    


        let geocoder = new kakao.maps.services.Geocoder(); //    

        geocoder.addressSearch(searchValue, placesSearchCB)
        
        //       
        function placesSearchCB (data, status, pagination) {
            if (status === kakao.maps.services.Status.OK) {

            
            //       
            // LatLngBounds   
            let coords = new kakao.maps.LatLng(data[0].y, data[0].x);

            displayMarker(coords);

            //       
            map.setCenter(coords);
            };
        };

        //    
        function displayMarker(place) {
    
            //    
            let marker = new kakao.maps.Marker({
                map: map,
                position: place
            });

            let infowindow = new kakao.maps.InfoWindow({
                content : '<div style="width:150px;text-align:center;padding:6px 0;">~</div>'
            });
            infowindow.open(map, marker);

            // //   
            // kakao.maps.event.addListener(marker, 'click', function() {
            //     //     
            //     infowindow.setContent('<div style="padding:5px;font-size:12px;">' + place.place_name + '</div>');
            //     infowindow.open(map, marker);
            // });
        };

    }, [searchValue]);

    


    return(
        <>
        <Navigation />
        <div className="bu">
        <input type="text" id="input" onChange={changeLocation}></input>
        <button onClick={callUse}></button>
        <button onClick={resetMap}></button>
        </div>
        <div id="map" style={{width:"598px", height:"673px"}}></div>
        </>
    );
};

export default Itermap;


